import './App.css';
import UserChart from "./UserChart";
import {useState} from "react";

function App() {
  const [userCounts, setUserCounts] = useState([{"id": '  ',
    "color": "hsl(130, 70%, 50%)",
    "data": [
      { x: '2023-1', y: 0 },
      { x: '2023-2', y: 0 },
      { x: '2023-3', y: 0 },
      { x: '2023-4', y: 0 },
      { x: '2023-6', y: 0 },
      { x: '2023-7', y: 0 },
      { x: '2023-8', y: 0 },
      { x: '2023-9', y: 0 },
      { x: '2023-10', y: 0 },
      { x: '2023-11', y: 0 },
      { x: '2023-12', y: 0 }
    ]}]);

  fetch(`${process.env.REACT_APP_SERVER_DOMAIN}/api/admins/users/count`)
      .then(data => data.json())
      .then(data => data.map(({date, count}) => ({ x: date, y: count })))
      .then(data => setUserCounts([{"id": '  ',
        "color": "hsl(130, 70%, 50%)",
        "data": data}]))
      .catch(err => console.error(err))

  return (
    <div className="App">
      <header>
        <h1> ! ~   </h1>
      </header>
      <main>
        <div className={"mainContainer"}>
          <h2>    </h2>
          <div style={{height: '500px'}}>
            <UserChart data={userCounts} />
          </div>
        </div>
        <div className={"mainContainer"}>
          <h2>    </h2>
          <div style={{height: '500px'}}>
            <UserChart data={userCounts} />
          </div>
        </div>
      </main>
    </div>
  );
}

export default App;
